import { v4 as uuid } from 'uuid';
import { Server, Req } from 'one-server';

export class HAServer implements Server {
  private key: string;
  private monitor: boolean;
  private frequency: number;

  private servers: {
    id: string,
    base: string,
    health: string,
    active: boolean,
    monitored: false,
  }[];

  constructor(
    servers: any,
  ) {
    const {
      monitor = true,
      frequency = 10000,
      locations = [],
    } = servers;

    this.key = uuid();
    this.monitor = monitor;
    this.frequency = frequency;

    this.servers = locations.map((location: any) => ({
      ...location,
      id: uuid(),
      active: true,
      monitored: false,
    }));

    console.debug(
      'one-server',
      'instance initialized',
      JSON.stringify({
        ...this,
      }),
    );
  }

  public forget(): Promise<any> {
    this.key = uuid();
    return;
  }

  /**
   * Helper to build a query string
   * (data is uri encoded)
   * @param data data to be converted to a query string
   */
  protected query = (data: any): string => {
    let path = '';
    if (data) {
      const entries: any = Object.entries(data);
      if ((entries) && (entries.length > 0)) {
        let sep = '?';
        for (const [name, value] of entries) {
          if (value) {
            const encoded = encodeURIComponent(value);
            path = `${path}${sep}${name}=${encoded}`;
            sep = '&';
          }
        }
      }
    }
    return path;
  }

  protected gatewayError = (status: number): boolean => {
    return (
      (status == 502) || (status == 503) || (status == 504)
    );
  }

  protected checkHealth = (
    server: any, frequency: number,
  ): void => {
    const { base, health } = server;
    const url = (health) || (`${base}healthcheck`);

    console.debug(
      'one-server-health',
      'setting up monitor',
      JSON.stringify({
        url,
        server,
        frequency,
      }),
    );

    setTimeout(async () => {
      console.debug(
        'one-server-health',
        'time to check',
        JSON.stringify({
          url,
          server,
          frequency,
        }),
      );

      try {
        const { status } = await fetch(url);
        // gateway error
        if (this.gatewayError(status)) {
          console.debug(
            'one-server-health',
            'gateway error, mark inactive, re-start montior',
            JSON.stringify({
              url,
              status,
              server,
              frequency,
            }),
          );
          this.checkHealth(server, frequency);
        } else {
          // mark active and
          // stop monitoring
          server.active = true;
          server.monitored = false;
          console.debug(
            'one-server-health',
            'valid response, mark active, stop monitor',
            JSON.stringify({
              url,
              status,
              server,
              frequency,
            }),
          );
        }
      } catch (error) {
        console.debug(
          'one-server-health',
          'not reachable, mark inactive, re-start montior',
          JSON.stringify({
            url,
            error,
            server,
            frequency,
          }),
        );
        this.checkHealth(server, frequency);
      }
    }, frequency);
  }

  protected update = (server: any, active: boolean): void => {
    server.active = active;
    const { monitor, frequency } = this;
    if ((monitor) && (!(active))) {
      const { monitored } = server;
      if (monitored) {
        console.debug(
          'one-server-health',
          'already monitored, ignore health check',
          JSON.stringify({
            monitor, frequency, server,
          }),
        );
      } else {
        console.debug(
          'one-server-health',
          'setting up health check monitor',
          JSON.stringify({
            monitor, frequency, server,
          }),
        );
        this.checkHealth(server, frequency);
        server.monitored = true;
      }
    }
  }

  protected handle(req: Req, method: string): Promise<any> {
    const key = this.key;

    return new Promise(async (resolve, reject) => {
      const list = [];
      const { servers, monitor } = this;

      // try active servers first
      // inactive servers will be made active
      // by the monitor thread
      for (const server of servers) {
        const { active } = server;
        if (active) {
          list.push(server);
        }
      }

      // no monitoring
      // try inactive servers as well
      if (!(monitor)) {
        for (const server of servers) {
          const { active } = server;
          if (!(active)) {
            list.push(server);
          }
        }
      }

      // flag request was handled
      let handled: boolean = false;

      // try the request on available servers
      for (const server of list) {
        // build the request
        const {
          path, data, retry = true,
        } = req;
        const { id, base } = server;

        let url, body;
        if (method === 'GET') {
          url = `${base}${path}${this.query(data)}`;
        } else {
          url = `${base}${path}`;
          body = JSON.stringify(data);
        }

        console.debug(
          'one-server',
          'request url', JSON.stringify({
            id, url, method, data,
          }),
        );

        try {
          const resp = await fetch(url, {
            method,
            body,
          });

          const { status } = resp;
          const { key: currKey } = this;

          // make sure the transaction key
          // has changed in between
          if (key !== currKey) {
            handled = true;
            // reject response
            reject({
              status: -1,
            });

            console.debug(
              'one-server',
              'ignored, keys do not match',
              JSON.stringify({
                id, url, key, currKey,
              }),
            );

            if (this.gatewayError(status)) {
              console.debug(
                'one-server',
                'ignored, keys do not match, gateway error',
                JSON.stringify({
                  id, url, key, currKey,
                }),
              );
              // server not reachable
              this.update(server, false);
            } else {
              // just mark the server active 
              this.update(server, true);
            }
          } else {
            console.debug(
              'one-server',
              'accepted, keys match',
              JSON.stringify({
                id, url, key, currKey,
              }),
            );

            if (this.gatewayError(status)) {
              console.debug(
                'one-server',
                'accepted, keys match, gateway error',
                JSON.stringify({
                  id, url, key, currKey,
                }),
              );
              // server not reachable
              this.update(server, false);
            } else {
              handled = true;
              this.update(server, true);

              // extract the response
              // TODO: add proper error handling
              let data:any;
              try {
                data = await resp.json();
              } catch (exp) {
              }

              // resolve the promise
              if (resp.ok) {
                const results = (data) && (data.results);
                resolve(results);
              } else {
                const errors = (data) && (data.error);
                reject({
                  status, errors,
                });

                console.debug(
                  'one-server',
                  'request error',
                  JSON.stringify({
                    id, key, url, status, data,
                  }),
                );
              }
            }
          }
        } catch (error) {
          // network/infrastructure error
          // mark the server inactive 
          this.update(server, false);
          if (!(retry)) {
            // do not retry
            break;
          }
        }

        // break out of the loop
        if (handled) {
          // done
          break;
        }
      }

      if (!(handled)) {
        // request was not handled
        reject({
          status: 0,
          errors: [{
            error_description: 'Unable to connect, server(s) not available'
          }]
        });
      }
    });
  }

  public get(req: Req): Promise<any> {
    // make a GET request
    return this.handle(req, 'GET');
  }

  public post(req: Req): Promise<any> {
    // make a POST request
    return this.handle(req, 'POST');
  }
}
