
export enum Types {
    SET = 'one-hotkey-set',
    RESET = 'one-hotkey-reset',
};

export interface Action<T, P = any> {
    type: T,
    payload: P,
}

const initial: any = {
    active: false,
    dnis: undefined,
};

export const reducer = (context: any) => {
    const { log } = context;
    return (state: typeof initial = initial, action: Action<Types>) => {
        console.debug (
            'hotkeys', action,
        );

        let updated = state;
        const { type, payload } = action;

        switch (type) {
            case Types.SET:
                updated = {
                    ...payload,
                };
                break;

            case Types.RESET:
                updated = initial;
                break;
        }
        return updated;
    }
}
