import * as React from 'react';
import { Provider as RRP } from 'react-redux';
import { Context } from 'one-ui-context';

interface Props {
    store: any;
    context: any;
    children?: JSX.Element[] | JSX.Element;
}

export const Provider = (props: Props) => {
    const {
        store,
        context,
        children,
    } = props;
    
    return (
        <RRP store={store}>
            <Context.Provider value={context}>
                {children}
            </Context.Provider>
        </RRP>
    );
};
