import * as React from "react";
import { Props as BaseProps } from 'one-ui';

interface Props extends BaseProps {
    title: string;
    icon?: string;
    width?: string;
    height?: string;
}

export const CardTitle = (props: Props) => {
    const {
        title,
        icon,
        width,
        height = '32px',
        className = 'ov-card-title',
    } = props;

    return (
        <>
            <span className={`${className}-icon`}>
                <img
                    width={width}
                    height={height}
                    src={icon}
                />
            </span>
            <span className={`${className}-label`}>
                <p>
                    <strong>
                        {title}
                    </strong>
                </p>
            </span>
            <hr className={`${className}-sep`} />
        </>
    );
};
